	module.exports = {"data":{"markdownRemark":{"html":"<h2>Linter</h2>\n<p><img src=\"https://2.bp.blogspot.com/-w7oeP1geosE/V82a740bTbI/AAAAAAAAAu4/-zJxZsmmH6garbdmUplX0n5Yz5zDsvcVQCLcB/s1600/tslint.png\" alt=\"TSLint\"></p>\n<p>A <a href='https://www.wikiwand.com/en/Lint_(software' target='_blank' rel='nofollow'>linter</a> is defined as a tool that detects and flags errors in programming languages, including stylistic errors.</p>\n<p>For TypeScript, <a href='http://palantir.github.io/tslint' target='_blank' rel='nofollow'>TSLint</a> is the most popular linter option.</p>\n<p>TSLint is an extensible static analysis tool that checks TypeScript code for readability, maintainability, and functionality errors.</p>\n<p>It is widely supported across modern editors &#x26; build systems and can be customized with your own lint rules, configurations, and formatters.</p>\n<h3>Installing TSLint</h3>\n<p>This command will globally install the <code>TSLint</code> package using <code>npm</code>, a popular package manager.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-bash\"><code><span class=\"token function\">npm</span> i -g tslint\n</code></pre>\n      </div>","fields":{"slug":"/typescript/linter/"},"frontmatter":{"title":"Linter"}}},"pathContext":{"slug":"/typescript/linter/"}}

